.input{
    display: flex;
    width: 90%;
    position: relative;
    align-items: center;
}

.input__box{
    width: 100%;
    border-radius: 50px;
    padding: 20px 30px;
    font-size: 25px;
    border: none;
    transition: 0.2s;
    box-shadow: inset 0 0 5px black;
}

.input__box:focus{
    box-shadow: 0 0 10px 1000px rgba(0,0,0,0.5);
    outline: none;
}

.input__submit{
    position: absolute;
    width: 50px;
    height: 50px;
    margin: 12px;
    border-radius: 50px;
    right: 0px;
    border: none;
    font-size: 15px;
    background-color: #2f74c0;
    color: white;
    transition: 0.2s all;
    box-shadow: 0 0 10px black;
}

.input__submit:hover{
    background-color: #388ae2;
}
.input__submit:active{
    transform: scale(0.8);
    box-shadow: 0 0 5px black;
}
.todos{
    display: flex;
    justify-content: space-evenly;
    width: 90%;
    flex-wrap: wrap;
}

.todos__single {
    display: flex;
    width: 29.5%;
    border-radius: 5px;
    padding: 20px;
    margin-top: 15px;
    background-color: rgb(255, 174, 0);
}

.todos__single--text{
    flex: 1;
    padding: 5px;
    border: none;
    font-size: 25px;
}
.todos__single--text:focus{
    outline: none;
}
.icons{
    margin-left: 10px;
    font-size: 25px;
    cursor: pointer;
}

@media (max-width: 1200px){
    .todos__single{
        width: 40%;
    }
}
@media (max-width: 700px) {
    .input{
        width: 95%;
    }
    .todos{
        width: 95%;
    }
    .todos__single{
        width: 100%;
    }
}